@import '../../../style/variables.scss';

.aside{
    max-width: 300px;
    width: 100%;
    padding: 20px;
    display: flex;
    border-right: 1px solid #ddd;
    height: 100vh;
    flex-direction: column;
    &-course-title { 
        &-img {
            img {
                margin-top: 30px;
                width: 150px;
                border-radius: 5px;
            }
        }
        &-name {
            font-weight: 800;
            font-size: 25px;
            margin-top: 20px;
            margin-top: 20px;
        }
        &-subtitle {
            margin-top: 20px;
        }
    }
    &-course-modules{
        &-title{
            font-weight: 800;
            margin-top: 20px;
            font-size: 20px;
        }
        ul{
            display: block;
            list-style-type: disc;
            margin-block-start: 0em;
            margin-block-end: 0em;
            margin-inline-start: 0px;
            margin-inline-end: 0px;
            padding-inline-start: 0px;
            li {
                list-style: none;
                margin: 10px 0px;
                border-top: #ddd 1px solid;
                padding-top: 10px;
                cursor: pointer;
                a {
                    color: $black;
                    text-decoration: none;
                    transition: all 0.3s ease-in-out;
                    &:hover {
                        color: $blue;

                    }
                }
            }
        }
    }

    &.open {
        @media(max-width: 1232px) {
            transform: translateX(0);
        }
    }

    &.closed {
        @media(max-width: 1232px) {
            transform: translateX(-200%);
        }
    }
    &-icon {
        display: none;
        position: fixed;
        z-index: 99999;
        font-size: 25px;    
        left: 20px;
        top: 10px;
        @media(max-width: 1232px) {
            display: block;
        }
    }
    @media (max-width: 1232px) {
        position: fixed;
        top: 0;
        left: 0;
        max-width: 350px;
        height: 100%;
        z-index: 9999;
        background-color: rgba(255, 255, 255, 1);
        overflow-y: auto;
        &.open {
            transform: translateY(0);
        }
        &.closed {
            transform: translateY(-100%);
        }
    }
    @media (max-width: 500px) {
        max-width: 100%;
    }
}

.aside-course-modules-logo{
    font-weight: 800;
    font-size: 30px;
    position: fixed;
    bottom: 10px;
    cursor:grab;
}