@import '../../../style/variables.scss';


.account {
    .account-main {
        
        padding: 30px;
        &-welcome {
            &-img {
                img{
                    width: 150px;
                }
            }
        }
        &-courses {
            &-all {
                display: flex;
                justify-content: space-between;
                gap: 30px;
                flex-wrap: wrap;
                @media(max-width: 1320px) {
                    justify-content: center;
                }
            }
        }
        &-progress {
            margin-top: 70px;
            display: flex;
            flex-direction: column;
            @media(max-width: 1320px) {
                align-items: center;
            }
        }
    }
    &-title {
        font-size: 30px;
        font-weight: 800;
        background: linear-gradient(89.97deg, $blue 1.84%, $orange 102.67%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        margin-bottom: 20px;
        @media(max-width: 576px) {
            text-align: center;
        }

    }
    &-page-title {
        margin-top: 30px;
        font-size: 35px;
        font-weight: 800;
        background: linear-gradient(89.97deg, $black 1.84%, $orange 102.67%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-align: right;
        @media(max-width: 576px) {
            margin-top: 90px;
            text-align: center;
        }
    }
}
