.course{
    display: flex;
    .course-main {
        margin-top: 20px;
        text-align: center;
        width: 100%;
        padding: 70px;
        @media(max-width: 516px) {
            padding: 20px;
        }
        h3{
            font-size: 40px;
            @media(max-width: 516px) {
                font-size: 20px;
            }
        }
        &-title {
            margin: 20px 0px;
        }
        &-descr {
            margin: 20px 0px;
            p {
                margin-top: 20px;
            }
        }
        &-img {
            display: flex;  
            align-items: center;
            justify-content: center;
            width: 100%;
            img{
                width: 300px;
                @media(max-width: 516px) {
                    width: 150px;
                }
            }
        }
    }

}