@import '../../style/variables.scss';

.progress-card{
    width: 300px;
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 30px;
    text-align: center;
    margin-bottom: 20px;
    &-icon{
        img {
            
        }
    }
    &-title {
        font-size: 20px;
        font-weight: 700;
        background: linear-gradient(89.97deg, $blue 1.84%, $orange 102.67%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    &-descr {
        font-size: 13px;
        color: $black;
    }
}